<template>
  <div class="network-wrap">
    <h2>网点查询</h2>
    <div class="network-main-wrap">
      <div class="ui-select">
        <select v-model="klass" @change="changeSelect">
          <option
            v-for="(item, index) in network"
            :key="index"
            :value="item.cn_code"
          >{{item.cn}}</option>
        </select>
        <select v-model="sheng" @change="changeSelect">
          <option
            v-for="(item, index) in getShengShi"
            :key="index"
            :value="item.label"
          >{{item.value}}</option>
        </select>
        <select v-model="shi" @change="changeSelect">
          <option
            v-for="(item, index) in shiList"
            :key="index"
            :value="item.label"
          >{{item.value}}</option>
        </select>
      </div>
      <div class="service-cont">
        <network-item
          v-for="(item, index) in stationList"
          :key="index"
          :station="item"
        ></network-item>
      </div>
    </div>
    <mt-actionsheet
      :actions="actions"
      v-model="sheetVisible">
    </mt-actionsheet>
  </div>
</template>

<script>
import NetworkItem from './network-item.vue'
import {fetchData} from '../../../api/base'
import {MaintainUrl} from '../../../config/urls'
import {pushIntention} from '../../../api/maintain'
import shengshi from '../../../../static/json/shengshi.json'
export default {
  name: 'Network',
  data () {
    return {
      network: [],
      stationList: [],
      klass: '04',
      sheng: '请选择',
      shiList: [],
      shi: '',
      actions: [],
      sheetVisible: false
    }
  },
  created () {
    this.getDatas()
  },
  mounted () {
    this.$on('selectSomeoneService', (station) => {
      pushIntention(station.cust_code, 3, 2)
      this.sheetVisible = true
      this.$set(this, 'actions', [{
        name: '高德地图',
        method: function () {
          window.location.href = 'http://uri.amap.com/navigation?to=' + station.lng + ',' + station.lat + ',' + station.cust_name + '&mode=car&callnative=1'
        }
      }, {
        name: '百度地图',
        method: function () {
          window.location.href = 'http://api.map.baidu.com/marker?location=' + station.lat + ',' + station.lng + '&title=目的位置&content=' + station.company_address + '&output=html'
        }
      }])
    })
  },
  computed: {
    getShengShi: function () {
      return shengshi
    }
  },
  watch: {
    sheng: function (val) {
      shengshi.forEach((item, index) => {
        if (item.value === val) {
          this.$set(this, 'shiList', shengshi[index].children)
        }
      })
    }
  },
  methods: {
    getDatas () {
      fetchData('get', MaintainUrl.maintainNetworkLabelList, null)
        .then(data => {
          this.network = data.data.list
        })
      fetchData('get', MaintainUrl.maintainNetworkStationList, null)
        .then(data => {
          this.stationList = data.data.list
        })
    },
    changeSelect () {
      if (this.sheng === '请选择') {
        fetchData('post', MaintainUrl.maintainNetworkStationList, {
          cn_code: this.klass,
          province: '',
          city: this.shi
        })
          .then(data => {
            this.stationList = data.data.list
          })
      } else {
        fetchData('post', MaintainUrl.maintainNetworkStationList, {
          cn_code: this.klass,
          province: this.sheng,
          city: this.shi
        })
          .then(data => {
            this.stationList = data.data.list
          })
      }
    }
  },
  components: {
    NetworkItem
  }
}
</script>

<style lang="less" scoped>
  .network-wrap{
    height: 100%;
    background-color: #e5e5e5;
    h2{
      font-size: 24px;
      height: 87px;
      margin: 0;
      line-height: 87px;
      text-align: center;
      color: blue;
      border-bottom: 1px solid #dddddd;
      background-color: #fff;
    }
    .network-main-wrap{
      width: 100%;
      overflow: hidden;
      background-color: #fff;
      &:after{
        display: block;
        width: 100%;
        height: 5rem;
        font-size: .8rem;
        content: '我也是有底线的...';
        border-bottom: 1px solid #ddd;
        color: #999;
        text-align: center;
        background-color: #eee;
      }
      .ui-select {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        margin: 15px;
        color: #4f5864;
        font-size: 14px;
        select{
          flex: 1;
          border: 1px solid #dedede;
          height: 29px;
          line-height: 29px;
        }
      }
      .service-cont{
        padding: .6em;
        font-size: 13px;
        background-color: #f3f5f6;
      }
    }
  }
</style>



