<template>
  <div class="network-item" v-on:click="selectService">
    <p><a href="javascript:void(0)">{{station.cust_name}}</a></p>
    <p>电话:{{station.company_tel}}</p>
    <p>地址：{{station.company_address}}</p>
  </div>
</template>

<script>
export default {
  name: 'Network-Item',
  props: {
    station: Object
  },
  methods: {
    selectService () {
      this.$parent.$emit('selectSomeoneService', this.station)
    }
  }
}
</script>

<style lang="less" scoped>
  .network-item{
    color: #4f5864;
    font-size: 12px;
    border-bottom: 1px solid #ddd;
    p{
      margin: .4rem 0;
      line-height: 1.7;
      color: #999;
      a {
        color: #333;
        font-size: 14px;
      }
    }
  }
</style>



